import React, { Component } from 'react';
import InitialState from '../data/initialState';
import profilePic from '../assets/profile.jpg';

class Navbar extends Component {

    constructor(props) {
        super(props);
        this.state = { navItems: InitialState.sections.navbar }
    }

    render(){
        return (
            <nav className="navbar navbar-expand-lg navbar-dark text-center fixed-top" id="portfolio-nav">
            <a className="navbar-brand text-accent" href="#"><img className="profile-pic" src={profilePic} alt="Profile Pic"></img> <span style={{ fontSize: "75%" }}>&nbsp;Deepankar Jaisia</span></a>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
            <i className="fas fa-bars text-white"></i>
            </button>
            <div className="collapse navbar-collapse" id="navbarText">
                <ul className="navbar-nav ml-auto">
                { this.state.navItems.map((item,i) => <li className="nav-item" key={item.Link}><a data-scroll className="nav-link" href={item.Link}>{item.Title}</a></li>) }
                </ul>
            </div>
            </nav>
        )
    }
}

export default Navbar;


