import React,{ Component } from 'react';
import TypedMessage from './TypedMessage.jsx';

class AppBanner extends Component {

    render() {
        return(
            <section className="portfolio-banner" style={{ display: "flex", alignItems: "center" }}>
              <div className="container">
              <div className="row">
                <div className="col-12 col-md-6">
                <h1 className="text-white text-center text-md-right">
                  I am
                </h1>
                </div>
                <div className="col-12 col-md-6">
                {/* <Typist>{this.state.typed[1]}<Typist.Delay ms={500} /></Typist> */}
                <h1 className="text-black-70 text-center text-md-left">
                    <TypedMessage />
                </h1>
                </div>
              </div>
              </div>
            </section>
        )
    }
}

export default AppBanner;


